@import url(https://fonts.googleapis.com/css2?family=Play&display=swap);
.btn {
  cursor: pointer;
  background-color: #f3f3fb;
  font-size: 15px;
  width: 55px;
  height: 40px;
  color: #242c44;
  font-weight: 700;
  position: relative;
  transition: transform 150ms ease-in-out;
  border-radius: 0.25rem;
  outline: none;
  border: none;
  border-bottom: 2px solid #242c44;
}
.btn:active {
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
  transform: translateY(4px);
}

.main-page {
  height: 100vh;
  background-color: #f3f3fb;
}
.navbar {
  height: 10vh;
  width: 1100px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.logo {
  font-size: 45px;
  letter-spacing: 3px;
  color: #242c44;
}
.gamepage__namepart {
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.gamepage__number {
  height: 10vh;
  text-align: center;
}
.game-part {
  height: 70vh;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.card {
  height: 50vh;
  width: 50%;
  background-color: white;
  border-radius: 13px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.title > lable {
  padding: 0px 20px;
  font-size: 30px;
  font-weight: 800;
  color: #242c44;
}

.content {
  width: 80%;
}
.raw {
  display: flex;
  justify-content: space-evenly;
  margin: 10px 0px;
}

.btn-big {
  cursor: pointer;
  background-color: #242c44;
  font-size: 15px;
  padding: 10px 15px;
  color: white;
  border-radius: 5px;
  transition: 0.4s;
  letter-spacing: 1px;
  border: 1px solid #242c44;
}
.btn-big:hover {
  background-color: white;
  color: #242c44;
  border: 1px solid #242c44;
}

.turn {
  color: red;
  font-weight: 700;
}

@media screen and (max-width: 800px) {
  .card {
    width: 100%;
  }
  .game-part {
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .navbar {
    height: 20vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
}

.main-content {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f3f3fb;
}
.card-home {
  height: 50vh;
  width: 30%;
  background-color: white;
  border-radius: 13px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid #242c44;
}
.btn-mid {
  cursor: pointer;
  border: none;
  font-size: 15px;
  width: 100px;
  height: 40px;
  border-radius: 5px;
  transition: 0.4s;
  font-weight: bold;
  font-family: "Play", sans-serif;
}
.btn-dark {
  color: #f3f3fb;
  background-color: #242c44;
}
.btn-light {
  background-color: #f3f3fb;
  color: #242c44;
}

@media screen and (max-width: 800px) {
  .card-home {
    width: 100%;
  }
}
